import React, { useCallback, useState } from 'react'
import { Container, Row, Col, Button } from 'react-bootstrap'
import { navigate } from 'gatsby'
import SignUpForm from './style'
import { Images } from '~data'
import { Link } from '~components'
import SectionTitle from './Components/SectionTitle'
import { SuperTag } from '~components';
import ReactTypingEffect from 'react-typing-effect';

import ThankYouPage from "./style1";


import axios from 'axios'



export default function Consultation() {

    const [submited, setSubmited] = useState(false)

    const [book, setBook] = useState({
        firstname: "",
        lastname: "",
        email: "",
        number: "",
        company: ""

    })

    const firstName = useCallback((event) => {
        setBook((prevState) => ({
            ...prevState,
            firstname: event.target.value
        }))
    })

    const lastName = useCallback((event) => {
        setBook((prevState) => ({
            ...prevState,
            lastname: event.target.value
        }))
    })
    const email = useCallback((event) => {
        setBook((prevState) => ({
            ...prevState,
            email: event.target.value
        }))
    })

    const number = useCallback((event) => {
        setBook((prevState) => ({
            ...prevState,
            number: event.target.value
        }))
    })

    const company = useCallback((event) => {
        setBook((prevState) => ({
            ...prevState,
            company: event.target.value
        }))
    })




    const submitNow = () => {

        console.log(book)

        axios.post("https://getform.io/f/7560ba27-f028-4c6d-ba69-4dd11c8fd9f5", {
            new: "nn",
            firstName: book.firstName,
            lastName: book.lastName,
            email: book.email,
            number: book.number,
            company: book.company,
        })
            .then(response => setSubmited(true))
            .catch(error => console.log(error))

    }






    if (submited) {
        return (

            <ThankYouPage backgroundColor="#f3f4f6">
                <Container>
                    <Row className="justify-content-center">
                        <Col xs="12" className="col-xl-6 col-lg-6 col-md-8 col-xs-10">

                            <ThankYouPage.Image className="error-page__image text-center ">
                                <img className="w-50 col-lg-3 col-xl-3 col-md-4 col-xs-5" src={Image} />
                            </ThankYouPage.Image>

                            <ThankYouPage.BigTitle as="h1">Thank You</ThankYouPage.BigTitle>
                            <ThankYouPage.Title>The form was submited successfully</ThankYouPage.Title>
                            {/* <ThankYouPage.Text>The page you are looking for is not available or<br className="d-none d-xs-block" /> doesn’t belong to this website!</ThankYouPage.Text> */}
                            <ThankYouPage.ButtonGroup>
                                <ThankYouPage.Button as={Link} className="btn-readical-red btn-1" to="/book" onClick={() => setSubmited(false)} >Go back</ThankYouPage.Button>
                                {/* <ThankYouPage.Button className="btn-outline-readical-red btn-2" to="#">Contact us</ThankYouPage.Button> */}
                            </ThankYouPage.ButtonGroup>
                        </Col>
                    </Row>
                </Container>

            </ThankYouPage>

        )

    } else {




        return (
            <SignUpForm backgroundColor="#f3f4f6">
                <Container className="position-static">
                    <Row className="align-items-center justify-content-center position-static px-3">

                        <Col xs="12" className="col-xl-6 col-lg-5 position-static">
                            {/* <SignUpForm.Content mt="40px" mtLG="0" mb="50px" mbLG="0" mlLG="30px">
                            <SectionTitle
                                title="Developing SUPERB Software"
                                text="If we do not deliver what we promise, we do the rest of the work for free. No questions asked!"
                                titleProps={{ mb: "20px" }}
                                subTitleProps={{ mb: "25px" }}
                            />
                        </SignUpForm.Content> */}


                            <SignUpForm.Content.TextBlock>
                                <SignUpForm.Content.Title as="h2"> <SuperTag value="Bring your <br className='d-none d-lg-block' /> idea into 
               
                <br className='d-none d-lg-block' />
                 "

                                />
                                    <div style={{ color: "#8a55df" }} >
                                        <ReactTypingEffect
                                            text={["finger tips.", "finger tips."]}
                                            className="highlight-text d-lg-block "
                                            speed="150"
                                            eraseSpeed="100"
                                            typingDelay="400"
                                            eraseDelay="800"
                                            cursorClassName="typed-cursor"
                                        />
                                    </div>



                                </SignUpForm.Content.Title>

                                <SignUpForm.Content.Text> <SuperTag value="You have the freedom to imagine and generate ideas. Discuss them freely with our tech experts and make it a reality. Experience the idea with your digital screen." /> </SignUpForm.Content.Text>
                            </SignUpForm.Content.TextBlock>




                        </Col>


                        <Col xs="12" className="col-xxl-5 col-xl-5 col-lg-6 col-md-9   shadow  rounded-2 p-4">
                            <SignUpForm.Box plXXL="60px">
                                <SignUpForm.Title as="h2">Book A Consultation</SignUpForm.Title>
                                <SignUpForm.Text as="p">Get a 30-minute free consultation to discover how to bring your ideas to finger tip.</SignUpForm.Text>
                                <br />
                                <SignUpForm.FromSection>
                                    <form onSubmit={submitNow}  >
                                        <div className="form-floating">
                                            <input className="form-control" type="text" placeholder="First Name" id="floatinginput1" name="fistname" onChange={firstName} />
                                            <label htmlFor="floatinginput1">First Name</label>

                                        </div>
                                        <div className="form-floating">
                                            <input className="form-control" type="text" placeholder="Last Name" id="floatinginput1" name="lastname" onChange={lastName} />
                                            <label htmlFor="floatinginput1">Last Name</label>
                                        </div>
                                        <div className="form-floating">
                                            <input className="form-control" type="email" placeholder="Your Email" id="floatinginput2" name="email" onChange={email} />
                                            <label htmlFor="floatinginput2">Your Email</label>
                                        </div>
                                        <div className="form-floating">
                                            <input className="form-control" type="text" placeholder="Phone Number" id="floatinginput3" name="number" onChange={number} />
                                            <label htmlFor="floatinginput3"> Phone Number</label>
                                        </div>
                                        <div className="form-floating">
                                            <input className="form-control" type="text" placeholder="Company Name" id="floatinginput4" name="company" onChange={company} />
                                            <label htmlFor="floatinginput4">Company Name</label>
                                        </div>
                                        <div className="form-check d-flex align-items-center mt-6 mb-3">
                                            {/* <input className="form-check-input bg-white float-none mt-0 mb-0 me-3" type="checkbox" defaultValue id="flexCheckDefault" /> */}
                                            {/* <SignUpForm.FormText>I agree to all the statements included in<Link className="btn-link text-electric-violet-2 ms-2" to="/privarcy">privacy policy</Link>
                                        </SignUpForm.FormText> */}
                                        </div>
                                        {/* <SignUpForm.FormButton className="btn-primary mt-2" onClick={submit} >Submit</SignUpForm.FormButton> */}
                                    </form>
                                    <ThankYouPage.Button className="btn-readical-red btn-1" onClick={submitNow}>Submit</ThankYouPage.Button>

                                    <br />
                                </SignUpForm.FromSection>
                            </SignUpForm.Box>
                        </Col>
                    </Row>
                </Container>
            </SignUpForm>
        )
    }
}
