import React from 'react'

import { Tab, Nav, Container, Col, Row, Card } from "react-bootstrap";
import SectionTitle from './Components/SectionTitle'

import Feature from './style'
import './style.css'

import cssImg from "~assets/image/book/web/css.png"
import htmlImg from "~assets/image/book/web/html.png"
import nestImg from "~assets/image/book/web/nest.png"
import nodeImg from "~assets/image/book/web/node.png"
import reactImg from "~assets/image/book/web/react.png"

import androidImg from "~assets/image/book/mobile/android.png"
import appleImg from "~assets/image/book/mobile/apple.jpg"
import flutterImg from "~assets/image/book/mobile/flutter.png"
// import reactImg from "~assets/image/book/mobile/react.png"

import postgresqlImg from "~assets/image/book/database/postgresql.png"
import dynamodbImg from "~assets/image/book/database/dynamodb.png"
import mongodbImg from "~assets/image/book/database/mongodb.png"
import mysqlImg from "~assets/image/book/database/mysql.png"

import awsImg from "~assets/image/book/cloud/aws.png"
import dockerImg from "~assets/image/book/cloud/docker.png"
import kubernatiesImg from "~assets/image/book/cloud/kubernaties.png"
import linuxImg from "~assets/image/book/cloud/linux.png"


import ethereumImg from "~assets/image/book/blockchain/ethereum.png"
import solanaImg from "~assets/image/book/blockchain/solana.png"
import solidityImg from "~assets/image/book/blockchain/solidity.png"
import web3jsImg from "~assets/image/book/blockchain/web3js.png"




export default function Expertise() {
  return (
    <Feature background="#fff">


      <Container className="container">


        <Row className="row justify-content-center">
          <Col className="col-lg-9 col-xl-9 col-xxl-7 text-center">
            <SectionTitle
              title="Our Technical Expertise"

              subTitleProps={{ mb: "27px" }}
              titleProps={{ mb: "10px", as: "h2" }}
              mb="20px" />
          </Col>
        </Row>




        <Tab.Container
          id="left-tabs-example"
          defaultActiveKey="first"
        >
          <Feature.Tab>
            <Row className="justify-content-center">
              <Col className="col-xl-7 col-lg-8 col-md-10">
                <Nav className="nav nav-tabs" id="myTab" role="tablist">
                  <Nav.Link
                    eventKey="first"
                    className="nav-link"
                  >Web
                  </Nav.Link>

                  <Nav.Link
                    eventKey="second"
                    className="nav-link"
                  >Mobile
                  </Nav.Link>
                  <Nav.Link
                    eventKey="third"
                    className="nav-link"
                  >Cloud
                  </Nav.Link>
                  <Nav.Link
                    eventKey="fourth"
                    className="nav-link"
                  >Blockchain
                  </Nav.Link>
                  <Nav.Link
                    eventKey="fifth"
                    className="nav-link"
                  >Database
                  </Nav.Link>

                </Nav>
              </Col>
            </Row>
            <Tab.Content className="tab-content">
              <Tab.Pane eventKey="first">

                <Row className="row justify-content-center p-1 m-1">

                  <Col xs="12" className="col-xl-2 col-lg-2 col-md-3 col-sm-6 col-xs-7 p-1 mx-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="top" src={reactImg} className="justify-content-center card-img-top w-100" />
                    </Card>
                  </Col>


                  <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7 p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="top" src={nodeImg} className="justify-content-center card-img-top w-100" />
                    </Card>
                  </Col>

                  <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7  p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="card-img-top" src={nestImg} className="justify-content-center card-img-top w-100" />
                    </Card>
                  </Col>

                  <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7  p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="card-img-top" src={htmlImg} className="justify-content-center card-img-top w-100" />

                    </Card>

                  </Col>
                  <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7  p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="card-img-top" src={cssImg} className="justify-content-center card-img-top w-100 " />

                    </Card>
                  </Col>
                </Row>


              </Tab.Pane>


              <Tab.Pane eventKey="second">

                <Row className="row justify-content-center p-1 m-1">

                  <Col xs="12" className="col-xl-2 col-lg-2 col-md-3 col-sm-6 col-xs-7 p-1 mx-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="card-img-top" src={flutterImg} className="justify-content-center card-img-top w-100 " />
                    </Card>
                  </Col>


                  <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7 p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="card-img-top" src={reactImg} className="justify-content-center card-img-top w-100 " />
                    </Card>
                  </Col>

                  <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7  p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="card-img-top" src={androidImg} className="justify-content-center card-img-top w-100 " />
                    </Card>
                  </Col>

                  <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7  p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="card-img-top" src={appleImg} className="justify-content-center card-img-top w-100 " />

                    </Card>

                  </Col>
                  {/* <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7  p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="top" src={cssImg} />

                    </Card>
                  </Col> */}
                </Row>







              </Tab.Pane>
              <Tab.Pane eventKey="third">



                <Row className="row justify-content-center p-1 m-1">

                  <Col xs="12" className="col-xl-2 col-lg-2 col-md-3 col-sm-6 col-xs-7 p-1 mx-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="top" src={awsImg} />
                    </Card>
                  </Col>


                  <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7 p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="top" src={dockerImg} />
                    </Card>
                  </Col>

                  <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7  p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="top" src={kubernatiesImg} />
                    </Card>
                  </Col>

                  <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7  p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="top" src={linuxImg} />

                    </Card>

                  </Col>
                  {/* <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7  p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="top" src={cssImg} />

                    </Card>
                  </Col> */}
                </Row>




              </Tab.Pane>

              <Tab.Pane eventKey="fourth">

                <Row className="row justify-content-center p-1 m-1">

                  <Col xs="12" className="col-xl-2 col-lg-2 col-md-3 col-sm-6 col-xs-7 p-1 mx-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="top" src={solidityImg} />
                    </Card>
                  </Col>


                  <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7 p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="top" src={web3jsImg} />
                    </Card>
                  </Col>

                  <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7  p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="top" src={ethereumImg} />
                    </Card>
                  </Col>

                  <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7  p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="top" src={solanaImg} />

                    </Card>

                  </Col>

                </Row>


              </Tab.Pane>


              <Tab.Pane eventKey="fifth">

                <Row className="row justify-content-center p-1 m-1">

                  <Col xs="12" className="col-xl-2 col-lg-2 col-md-3 col-sm-6 col-xs-7 p-1 mx-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="top" src={mongodbImg} />
                    </Card>
                  </Col>


                  <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7 p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="top" src={postgresqlImg} />
                    </Card>
                  </Col>

                  <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7  p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="top" src={mysqlImg} />
                    </Card>
                  </Col>

                  <Col xs="12" className="col-xl-2 col-lg-3 col-md-3 col-sm-6 col-xs-7  p-1">
                    <Card style={{ width: '15rem' }}>
                      <Card.Img variant="top" src={dynamodbImg} />

                    </Card>

                  </Col>

                </Row>

              </Tab.Pane>


            </Tab.Content>
          </Feature.Tab>
        </Tab.Container>
      </Container>
    </Feature>
  )
}